
/**
 * Contant used as empty color
 */
export const EMPTY_COLOR = 'none';

/**
 * This interface represents one color. Using this interface instead simple string
 * will help screen readers, because the text attribute ir set to the aria-label of
 * the option
 */
export interface MatColorPickerItem {
    text: string;
    value: string;
}

export type MatColorPickerOption = string | MatColorPickerItem;
/**
 * Verify if color has # as a first char. If not, add this char
 * to the color
 * @param {?} color string
 * @return {?}
 */
export function coerceHexaColor(color: string): string {
    if (!isValidColor(color)) {
        return EMPTY_COLOR;
    }

    if (color.indexOf('#') !== 0) {
        color = `#${color}`;
    }

    return color.toUpperCase();
}
/**
 * Validate if the color is valid
 * @param {?} color string
 * @return {?}
 */
export function isValidColor(color: string): boolean {
    // validate if color is an hexadecimal
    if (
        !color ||
        color.length < 6 ||
        color.length > 7
    ) {
        return false;
    }

    // validate rgb of the color
    const /** @type {?} */ hex = color.replace('#', '');
    const /** @type {?} */ r = parseInt(hex.slice(0, 2), 16);
    const /** @type {?} */ g = parseInt(hex.slice(2, 4), 16);
    const /** @type {?} */ b = parseInt(hex.slice(4, 6), 16);

    if (
        Number.isNaN(r) ||
        Number.isNaN(g) ||
        Number.isNaN(b)
    ) {
        return false;
    }

    return true;
}
